import axios from 'axios'

export const sendMail = ({ name, email, message }) => {
  const messageFormated = `name: ${name}\nemail: ${email}\n\n`
  const SENDGRID_API_TOKEN = process.env.SENDGRID_API_TOKEN
  var dataString = {
    personalizations: [{ to: [{ email: 'hello@octosante.fr' }] }],
    from: { email: 'hello@octosante.fr' },
    subject: `Octosanté contact from ${email} ${name}`,
    content: [{ type: 'text/plain', value: messageFormated.concat(message) }],
  }
  const config = {
    method: 'POST',
    url: 'https://api.sendgrid.com/v3/mail/send',
    headers: {
      Authorization: `Bearer ${SENDGRID_API_TOKEN}`,
      'Content-Type': 'application/json',
    },
    data: dataString,
  }
  return new Promise((resolve, reject) => {
    axios(config)
      .then(() => {
        resolve()
      })
      .catch(error => reject(error))
  })
}
